body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-lato: Lato;
  --font-montserrat: Montserrat;

  /* font sizes */
  --font-size-29xl: 48px;
  --font-size-13xl: 32px;
  --font-size-21xl: 40px;
  --font-size-45xl: 64px;
  --font-size-17xl: 36px;
  --font-size-77xl: 96px;
  --font-size-9xl: 28px;
  --font-size-33xl: 52px;
  --font-size-5xl: 24px;

  /* Colors */
  --color-white: #fff;
  --color-gray-100: #151515;
  --color-gray-200: rgba(0, 0, 0, 0.12);
  --color-gray-300: rgba(21, 21, 21, 0.59);
  --color-gray-400: rgba(21, 21, 21, 0.87);
  --color-gray-500: rgba(0, 0, 0, 0.54);
  --color-dimgray-100: #4e4c4f;
  --color-slategray-100: #408196;
  --color-slategray-200: rgba(64, 129, 150, 0.49);
  --color-black: #000;
  --color-darkslategray-100: #363636;
  --color-azure: #e0f3ee;
  --color-teal: #00838f;
  --color-pink: #f2b2d5;

  /* border radiuses */
  --br-3xs: 10px;
  --br-21xl: 40px;
  --br-31xl: 50px;
  --br-xl: 20px;
  --br-10xs: 3px;
  --br-7xl: 26px;
  --br-base: 16px;
}
